.upload-wrap{
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;
}

.upload-wrap__col{
    margin-right: 74px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;
    color: var(--color-dark);
    margin-bottom: 50px;
    img{
        margin-bottom: 15px;
    }
}
@media(min-width: 1200px){
    .upload-wrap--center{
        max-width: 100%;
        padding-right: 25px;
        .upload-wrap{
            width: 100%;
            justify-content: center;
        }
       
        .upload-wrap__col{
            margin-left: 50px;
            margin-right: 50px;
        }
    }
}

.upload-wrap__col:last-child{
    margin-right: 0;
}

.upload-wrap__btns{
    display: flex;
    flex-wrap: wrap;
    button{
        margin-right: 8px;
        margin-bottom: 8px;
    }
}
@media(max-width: 991px){
    .upload-wrap__col{
        padding: 0 15px;
        margin-right: 0;
        width: 50%;
        margin-bottom: 50px;
    }
}
@media(max-width: 767px){
    .upload-wrap{
        flex-direction: column;
        margin-top: 0;
        margin-bottom: 0;
    }
    .upload-wrap__col{
        margin-right: 0;
        margin-bottom: 50px;
        padding: 0;
        width: 100%;
    }
}

